* {
  margin: 0px;
  padding: 0px;
}
body {
  background: #34495e;
  font-family: 'Times New Roman';
}

h1 {
  font-size: 40px;
  margin-top: 20px;
  margin-bottom: 30x;
  color: #1ab188;
  text-align: center;
  font-weight: bold;
}

.form-control {
  background: transparent;
  margin-bottom: 10px;
  border: 0px;
  border-radius: 0;
  font-size: 19px;
  border-bottom: 1px solid #1ab188;
  color: 1ab188;
  height: 20px;
  overflow: hidden;
}

small {
  font-size: 13px;
  color: gray;
}
.btn-success {
  background: #1ab188;
  border-radius: 10px;
  border: 0px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 600px;
  height: 40px;
  font-size: 16px;
  transition: 1s;
}

.btn-success:hover {
  background: transparent;
  border: 2px solid #1ab188;
}

.color {
  font-size: 20px;
  margin-top: 12px;
  color: #1ab188;
  margin-left: 20px;
}

.col-md-6 {
  background-color: rgba(255, 255, 255, 0.1);
  margin-top: 20px;
  height: 750px;
  margin-right: auto;
  border-radius: 20px;
  width: 620px;
  margin-left: auto;
}

.form-label {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8);
  font-weight: bold;
}

input[type='radio'] {
  margin-left: 20px;
}
