@import 'https://maxcdn.bootstrapcdn.com/font-awesome/4.6.3/css/font-awesome.min.css';

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #34495e;
}

h3 {
  font-size: 40px;
  font-weight: bold;
  color: #1ab188;
}

.logo {
  float: left;
  border-bottom: 6px solid #4caf50;
  margin-bottom: 50px;
  padding: 23px 0;
}

.col-md-4 {
  padding-top: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  margin-top: 50px;
  height: 540px;
  margin-right: auto;
  border-radius: 20px;
  width: 500px;
  margin-left: auto;
}

.textbox {
  width: 100%;
  font-size: 10px;
  overflow: hidden;
  padding: 10px;
  margin: 8px;
  border-bottom: 1px solid #4caf50;
  margin-top: 50px;
}

.textbox i {
  width: 50px;
  float: left;
  text-align: left;
}

.textbox input {
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 15px;
  width: 80%;
  float: left;
  margin: 0 10px;
}

.btn-success {
  background: #1ab188;
  border-radius: 10px;
  border: 0px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 450px;
  height: 40px;
  font-size: 16px;
  transition: 2s;
}

.btn-success:hover {
  background: transparent;
  border: 2px solid #1ab188;
}

.signup {
  font-size: 15px;
  margin-top: 12px;
  color: #1ab188;
  margin-left: 10px;
}
